import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import axios from "axios";
import {useContext, useEffect, useState} from "react";
import MyContext from "../MyContext";

function Applications() {

    const context = useContext(MyContext);
    let [applications, setApplications] = useState([{
        "ong_id": 1,
        "pet_id":1,
        "name":"pepito",
        "situation" : 1
}])
    let [ongs, setOngs] = useState([])



    useEffect(() => {

        axios.get(context.cdn.api_gw + 'ongs')  .then(response => {
                const list = response.data;
                setOngs(list)
            })
            .catch(error => {
                console.error('Error al hacer la solicitud GET:', error);
            });
    }, [])

    useEffect(() => {
        axios.get(context.cdn.api_gw + 'applications')
            .then(response => {
                const list = response.data;
                setApplications(list)
            })
            .catch(error => {
                console.error('Error al hacer la solicitud GET:', error);
            });
    }, [ongs])

    return (
        <Row xs={1} md={2} className="g-4">
            <Row>
                {applications.map((app) => {
                        const ong = ongs.find((e) => e.ong_id === app.ong_id);
                    return (
                        <Col key={app.id}>
                            <Card>
                                <Card.Img variant="top" src="holder.js/100px160" />
                                <Card.Body>
                                    <Card.Title>ONG: {ong.name}</Card.Title>
                                    <Card.Title>PET'S NAME {app.name}</Card.Title>
                                </Card.Body>
                            </Card>
                    </Col>
                    );
            })}
        </Row>
        </Row>)
}

export default Applications;